.chart__wrap {
  position: relative; }

@media (max-width: 480px) {
  .chart__title {
    text-align: center; } }

.chartjs-render-monitor {
  border: 1px solid #edf0f1;
  padding-top: 10px; }

.chart__spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.chart__content_hidden {
  visibility: hidden; }
